




























































































import Vue from "vue";
import * as Three from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import Component from "vue-class-component";
import { Watch } from "vue-property-decorator";

@Component
export default class DrawWaves extends Vue {
  geometryItems: string[] = [
    "Flat",
    "Spike",
    "Diagonal Wall",
    "Side Wall",
    "Hole",
    "Middle Block",
    "Diagonal Block",
    "Corner Block",
    "Hill",
    "Hill Four"
  ];
  geometry = "Middle Block";
  numberOfPoints = 0;
  grid = 32;
  speed = 1;
  inverseSqrtTwo: number = Math.SQRT1_2;
  dt = 0;
  animate = false;
  step = false;
  positions: Float32Array = new Float32Array();
  velocity: Float32Array = new Float32Array();
  force: Float32Array = new Float32Array();
  camera: Three.PerspectiveCamera = new Three.PerspectiveCamera();
  scene: Three.Scene = new Three.Scene();
  drawing: Three.Points = new Three.Points();
  renderer: Three.WebGLRenderer = new Three.WebGLRenderer({
    antialias: true
  });
  controls: OrbitControls = new OrbitControls(
    this.camera,
    this.renderer.domElement
  );

  resetAnimation() {
    this.initPosition();
    this.drawing.geometry.attributes.position.needsUpdate = true;
    this.step = true;
    this.update();
  }
  stepAnimation() {
    this.step = true;
    this.update();
  }
  toggleAnimation() {
    this.animate = !this.animate;
    this.update();
  }
  updateGridSize() {
    this.initPosition();
    this.drawing.geometry.attributes.position.needsUpdate = true;
    this.numberOfPoints = this.grid * this.grid;
    this.drawing.geometry.setDrawRange(0, this.numberOfPoints);
    this.update();
  }
  updateGeometry() {
    this.initPosition();
    this.drawing.geometry.attributes.position.needsUpdate = true;
    this.update();
  }
  updateSpeed() {
    let scale = this.grid / 64;
    scale *= scale;
    this.dt = (this.speed / 100) * scale;
  }
  calculateForce() {
    for (let i = 0; i < this.numberOfPoints; ++i) {
      this.force[i] = 0;
    }
    let d;
    let loc;
    let pos;
    const xOff = this.grid * 3;
    const yOff = 3;
    for (let x = 1; x < this.grid - 1; ++x) {
      for (let y = 1; y < this.grid - 1; ++y) {
        loc = x * this.grid * 3 + y * 3 + 2;
        pos = x * this.grid + y;

        d = this.positions[loc] - this.positions[loc - yOff];
        this.force[pos] -= d;
        this.force[pos - 1] += d;

        d = this.positions[loc] - this.positions[loc + yOff];
        this.force[pos] -= d;
        this.force[pos + 1] += d;

        d = this.positions[loc] - this.positions[loc - xOff];
        this.force[pos] -= d;
        this.force[pos - this.grid] += d;

        d = this.positions[loc] - this.positions[loc + xOff];
        this.force[pos] -= d;
        this.force[pos + this.grid] += d;

        d =
          (this.positions[loc] - this.positions[loc - xOff - yOff]) *
          this.inverseSqrtTwo;
        this.force[pos] -= d;
        this.force[pos - this.grid - 1] += d;

        d =
          (this.positions[loc] - this.positions[loc - xOff + yOff]) *
          this.inverseSqrtTwo;
        this.force[pos] -= d;
        this.force[pos - this.grid + 1] += d;

        d =
          (this.positions[loc] - this.positions[loc + xOff + yOff]) *
          this.inverseSqrtTwo;
        this.force[pos] -= d;
        this.force[pos + this.grid + 1] += d;

        d =
          (this.positions[loc] - this.positions[loc + xOff - yOff]) *
          this.inverseSqrtTwo;
        this.force[pos] -= d;
        this.force[pos + this.grid - 1] += d;
      }
    }
  }
  calculateVelocity() {
    for (let i = 0; i < this.numberOfPoints; ++i) {
      this.velocity[i] = this.force[i] * this.dt;
    }
  }
  updatePosition() {
    for (let x = 0; x < this.grid; ++x) {
      for (let y = 0; y < this.grid; ++y) {
        const loc = x * this.grid * 3 + y * 3 + 2;
        this.positions[loc] += this.velocity[Math.floor(loc / 3)];
      }
    }
  }
  initAttributes() {
    this.positions = new Float32Array(64 * 64 * 3);
    this.velocity = new Float32Array(64 * 64);
    this.force = new Float32Array(64 * 64);
  }
  initPosition() {
    const size = this.grid - 1;
    for (let x = 0; x < this.grid; ++x) {
      for (let y = 0; y < this.grid; ++y) {
        const loc = x * this.grid * 3 + y * 3;
        this.positions[loc] = -1 + (2 * x) / size;
        this.positions[loc + 1] = -1 + (2 * (size - y)) / size;
        let s1, s2;
        switch (this.geometry) {
          case "Flat":
            this.positions[loc + 2] = 0;
            break;
          case "Spike":
            this.positions[loc + 2] =
              x == y && x == Math.round(this.grid / 2) ? 1.0 : 0.0;
            break;
          case "Hole":
            this.positions[loc + 2] = !(
              x > this.grid / 3 &&
              y > this.grid / 3 &&
              x < (this.grid * 2) / 3 &&
              y < (this.grid * 2) / 3
            )
              ? 0.0
              : -1.0;
            break;
          case "Diagonal Wall":
            this.positions[loc + 2] =
              this.grid - x - y < 3 && this.grid - x - y > 0 ? 1.0 : 0.0;
            break;
          case "Side Wall":
            this.positions[loc + 2] = x == 1 ? 1.0 : 0.0;
            break;
          case "Middle Block":
            this.positions[loc + 2] =
              x >= this.grid / 3 &&
              y >= this.grid / 3 &&
              x <= (this.grid * 2) / 3 &&
              y <= (this.grid * 2) / 3
                ? 1.0
                : 0.0;
            break;
          case "Diagonal Block":
            this.positions[loc + 2] = this.grid - x - y < 3 ? 1.0 : 0.0;
            break;
          case "Corner Block":
            this.positions[loc + 2] =
              x > (this.grid * 3) / 4 && y > (this.grid * 3) / 4 ? 1.0 : 0.0;
            break;
          case "Hill":
            s1 = Math.sin((Math.PI * x) / (this.grid - 1));
            s2 = Math.sin((Math.PI * y) / (this.grid - 1));
            this.positions[loc + 2] = (s1 + s2) * 0.3;
            break;
          case "Hill Four":
            s1 = Math.sin((2 * Math.PI * x) / (this.grid - 1));
            s2 = Math.sin((2 * Math.PI * y) / (this.grid - 1));
            this.positions[loc + 2] = (s1 + s2) * 0.3;
            break;
        }
      }
    }
  }
  initScene() {
    const container = document.getElementById("wave");
    if (container === null) {
      return;
    }
    this.camera = new Three.PerspectiveCamera(
      45,
      container.clientWidth / container.clientHeight,
      0.1,
      10
    );
    this.camera.position.x = 0;
    this.camera.position.y = 0;
    this.camera.position.z = 0;

    this.scene.background = new Three.Color("#000000");

    const geometry = new Three.BufferGeometry();
    //TODO: Init buffers here
    this.initAttributes();
    this.initPosition();
    this.updateSpeed();

    geometry.setAttribute(
      "position",
      new Three.BufferAttribute(this.positions, 3)
    );
    this.numberOfPoints = this.grid * this.grid;
    geometry.setDrawRange(0, this.numberOfPoints);

    //TODO: add colors here

    const vsShader = `
				void main () {
					gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
				}
			`;
    const fsShader = `
				void main () {
					gl_FragColor = vec4(1.0, 1.0, 1.0, 1.0);
				}
			`;

    const material = new Three.ShaderMaterial({
      vertexShader: vsShader,
      fragmentShader: fsShader
      // wireframe: true
    });

    this.drawing = new Three.Points(geometry, material);
    this.drawing.translateZ(-5);
    const q = new Three.Quaternion();
    q.setFromAxisAngle(new Three.Vector3(1, 1, 0), 150);
    this.drawing.setRotationFromQuaternion(q);

    this.scene.add(this.drawing);

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.enabled = true;
    this.controls.target.set(0, 0, -5);

    this.renderer.setSize(container.clientWidth, container.clientHeight);
    container.appendChild(this.renderer.domElement);
    this.renderer.render(this.scene, this.camera);
    this.update();
  }

  update() {
    if (this.animate) {
      this.calculateForce();
      this.calculateVelocity();
      this.updatePosition();
      this.drawing.geometry.attributes.position.needsUpdate = true;
    } else if (this.step) {
      this.calculateForce();
      this.calculateVelocity();
      this.updatePosition();
      this.drawing.geometry.attributes.position.needsUpdate = true;
      this.step = false;
    }
    this.controls.update();
    this.renderer.render(this.scene, this.camera);
    requestAnimationFrame(this.update);
  }

  init() {
    this.initScene();
  }

  mounted() {
    this.init();
  }

  @Watch("grid")
  gridChanged() {
    this.updateGridSize();
  }

  @Watch("geometry")
  geometryChanged() {
    this.updateGeometry();
  }

  @Watch("speed")
  speedChanged() {
    this.updateSpeed();
  }
}
