









import Vue from "vue";
import * as Three from "three";
import { BufferGeometryUtils } from "three/examples/jsm/utils/BufferGeometryUtils.js";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class P extends Vue {
  vertices = [
    7.0,
    7.5,
    0.0,
    7.5,
    6.5,
    0.0,
    7.0,
    5.5,
    0.0,
    2.5,
    5.5,
    0.0,
    2.5,
    4.5,
    0.0,
    7.5,
    4.5,
    0.0,
    8.0,
    5.5,
    0.0,
    8.5,
    6.5,
    0.0,
    8.0,
    7.5,
    0.0,
    7.5,
    8.5,
    0.0,
    1.5,
    8.5,
    0.0,
    1.5,
    1.5,
    0.0,
    1.0,
    1.0,
    0.0,
    3.0,
    1.0,
    0.0,
    2.5,
    1.5,
    0.0,
    2.5,
    7.5,
    0.0
  ];
  scene: Three.Scene = new Three.Scene();
  camera: Three.PerspectiveCamera = new Three.PerspectiveCamera();
  renderer: Three.WebGLRenderer = new Three.WebGLRenderer({
    antialias: true
  });

  initData() {
    const container = document.getElementById(this.id);
    if (container === null) {
      return;
    }
    this.camera = new Three.PerspectiveCamera(
      45,
      container.clientWidth / container.clientHeight,
      0.1,
      100
    );
    this.camera.position.x = 5;
    this.camera.position.y = 5;
    this.camera.position.z = 10;

    let geometry = new Three.BufferGeometry();

    geometry.setAttribute(
      "position",
      new Three.Float32BufferAttribute(this.vertices, 3)
    );

    let drawing;
    switch (this.id) {
      case "p_points":
        drawing = new Three.Points(geometry, this.material);
        break;
      case "p_line":
        drawing = new Three.LineSegments(geometry, this.material);
        break;
      case "p_line_strip":
        drawing = new Three.Line(geometry, this.material);
        break;
      case "p_line_loop":
        drawing = new Three.LineLoop(geometry, this.material);
        break;
      case "p_triangles":
        geometry = BufferGeometryUtils.toTrianglesDrawMode(
          geometry,
          Three.TrianglesDrawMode
        );
        drawing = new Three.Mesh(geometry, this.material);
        break;
      case "p_triangle_strip":
        geometry = BufferGeometryUtils.toTrianglesDrawMode(
          geometry,
          Three.TriangleStripDrawMode
        );
        drawing = new Three.Mesh(geometry, this.material);
        break;
      case "p_triangle_fan":
        geometry = BufferGeometryUtils.toTrianglesDrawMode(
          geometry,
          Three.TriangleFanDrawMode
        );
        drawing = new Three.Mesh(geometry, this.material);
        break;
      default:
        drawing = new Three.Mesh(geometry, this.material);
    }

    this.scene.add(drawing);

    this.renderer.setSize(container.clientWidth, container.clientHeight);

    container.appendChild(this.renderer.domElement);

    this.renderer.render(this.scene, this.camera);
  }

  init() {
    this.initData();
  }

  get material(): Three.Material {
    switch (this.id) {
      case "p_points":
        return new Three.PointsMaterial({
          color: "#0000ff"
        });
      default:
        return new Three.LineBasicMaterial({
          color: "#0000ff"
        });
    }
  }

  mounted() {
    this.init();
  }

  @Prop({ required: true, type: String, default: "p_points" })
  id!: string;
}
